import React from 'react';

const Kontakt = () => {
  return (
    <div id="Kontakt">
      <p>
        Ak by si sa k nm chcel prida a pomoc s vvojom, nevhaj a daj o sebe vedie. Pouvame
        modern technolgie ako React, JavaScript ES6, Node.js a NoSQL databzu. To vetko be na
        Heroku cloude a tak m vek ancu sa naui osi nov. Ak to ale u vetko pozn, me
        naui ty ns ako postavi CestaSNP.sk na nohy rchlo a efektvne.
      </p>
      <p>
        Kontakt na ns:{' '}
        <a target="_blank" href="mailto:info@cestasnp.sk">
          info@cestasnp.sk
        </a>.
      </p>
      <p>
        CestuSNP.sk njde na{' '}
        <a target="_blank" href="https://github.com/hasty001/cestasnp-web">
          Githube
        </a>.
      </p>
    </div>
  );
};

export default Kontakt;



